.Join
{

    display: flex;
    padding: 0 2rem;
    gap: 25rem;
}
.left-j
{
    color: white;
    text-transform: uppercase;
    font-size: 3rem;
    font-weight: bold;
    position: relative;
}
.left-j>hr
{
    position: absolute;
    width: 10.5rem;
    border: 2px solid var(--orange);
    border-radius: 20%;
    margin: -10px 0;
}
.left-j>div
{
    display: flex;
    gap: 1rem;
}
.right-j
{
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.email-container
{
    display: flex;
    gap: 3rem;
    background-color: var(--gray);
    padding: 1rem 2rem;
}
.email-container>input
{
    background-color: transparent;
    border: none;
    color: var(--lightgray);
    outline: none;
}
::placeholder
{
    color: var(--lightgray);
}
.btn-j
{
    background-color: var(--orange);
    color: white;
}
@media screen and (max-width: 768px){
    .Join{
        flex-direction: column;
        gap: 1rem;

    }
    .left-j{
        font-size: x-large;
        flex-direction: column;
    }
    .right-j{
        padding: 2rem;
    }
}